import React, { Component } from 'react'

import { __centered, getRandom } from '../utils'

import styled, { keyframes } from 'styled-components'
import { getRandomInt } from '../utils'

const colorAnimation = keyframes`
  0% {
    fill: ${getRandom('color')};
  }
  50% {
    fill: ${getRandom('color')};
  }
  100% {
    fill: ${getRandom('color')};
  }
`

const scaleAnimation = keyframes`
  0% {
    transform: scaleX(1) scaleY(1);
  }
  25% {
    transform: scaleX(3) scaleY(3);
  }
  50% {
    transform: scaleX(3) scaleY(3);
  }
  75% {
    transform: scaleX(1) scaleY(1);
  }
  100% {
    transform: scaleX(1) scaleY(1);
  }
`

const __breath = styled.circle`
  animation: ${colorAnimation}, ${scaleAnimation};
  animation-duration: 16s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  transform-origin: 50% 50%;
`

export class Breath extends Component {
  shouldComponentUpdate() {
    return false
  }

  render() {
    return (
      <__centered>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          height="300"
          width="300"
          viewBox="0 0 300 300"
          preserveAspectRatio="MidXMixY meet"
        >
          <__breath cx="50%" cy="50%" r="46" />
          <circle
            stroke={getRandom('color')}
            strokeWidth={getRandomInt(1, 6)}
            cx="50%"
            cy="50%"
            r="145"
            fill="none"
          />
        </svg>
      </__centered>
    )
  }
}



