import React, { Component } from 'react';

import './AnimatedEarth.css';

export class AnimatedEarth extends Component {
  shouldComponentUpdate() {
    return false
  }

  render() {
    return (
      <div className="AnimatedEarth">
        <div className="earth">
          <div className="earth--shadow" />
        </div>
        <div className="moon" />
        <div className="stars" />
      </div>
    );
  }
}



