import React, { Component } from 'react'

import { BrowserRouter as Router, Route, Link } from 'react-router-dom'

import { OfflineWorldScreen } from './offline-world-screen'
import { BreathScreen } from './breath-screen'
import { HomeScreen } from './home-screen'

import './App.css'

import styled, { injectGlobal } from 'styled-components'
import styledNormalize from 'styled-normalize'

import { media, unselectable, fib } from './utils'

import { emoji } from './utils'

import { ThumbPrint, LightDarkCircle } from './art'

/**
 * globally injected style scripts. will eventually replace App/index.css
 * @see https://www.styled-components.com/docs/api#injectglobal
 */
injectGlobal`
  ${styledNormalize}

  ${media.mobile`
    a, p, svg {
      ${unselectable}
    }
  `}
`

// prepending __ means styled component
const __theme = styled.div`
  background: ${props => (props.IS_DARK ? 'black' : 'white')};

  a {
    color: ${props => (props.IS_DARK ? 'white' : 'black')};
  }
`

const __toggle = styled.div`
  cursor: pointer;

  position: fixed;
  top: 0;

  z-index: 10;

  padding: ${fib[8] + 'px'};

  font-size: ${fib[10] + 'px'};

  ${media.mobile`
    font-size: ${fib[9] + 'px'};
    right: 0;
  `};
`

// nav
const __nav = styled.div`
  visibility: ${props => (props.IS_NAV_VISABLE ? 'visible' : 'hidden')};

  align-items: flex-end;
  align-content: center;
  display: flex;
  flex: 4;
  flex-direction: column;

  z-index: 10;

  max-height: 100vh;

  ${media.mobile`
    flex-direction: row;
    line-height: 55px;
  `} & > a {
    font-size: ${fib[10] + 'px'};
    flex: 2;
    text-decoration: none;

    cursor: pointer;

    text-align: center;

    ${media.mobile`
      font-size: ${fib[9] + 'px'};
    `} ${media.desktop`
      display: flex;
      justify-content: center;
      flex-direction: column;
      text-align: center;

      padding: 0 ${fib[7] + 'px'};
    `};
  }
`

export default class App extends Component {
  constructor(props) {
    super(props)

    this.state = { isDark: false, isNavVisible: false }
  }

  toggleTheme() {
    this.setState({ isDark: !this.state.isDark })
  }

  toggleNav() {
    this.setState({ isNavVisible: !this.state.isNavVisible })
  }

  render() {
    console.log(fib.length)
    const RenderApp = () => {
      return (
        <Router>
          <__theme IS_DARK={this.state.isDark} className="Section">
            <__nav IS_NAV_VISABLE={this.state.isNavVisible} onClick={this.toggleNav.bind(this)}>
              <Link to="/">{emoji.home}</Link>
              <Link to="/box-breath">{emoji.peace}</Link>
              <Link to="/offline-world">{emoji.globe}</Link>
            </__nav>

            <__toggle>
              <a onClick={this.toggleTheme.bind(this)}>{emoji.lightbulb}</a>
            </__toggle>

            <Route exact path="/" component={HomeScreen} />
            <Route exact path="/box-breath" component={BreathScreen} />
            <Route exact path="/offline-world" component={OfflineWorldScreen} />
          </__theme>
        </Router>
      )
    }

    return <RenderApp />
  }
}



