import React, { Component } from 'react';

const PROPS = {
  MID_EDGE: 'MID_EDGE',
  MID_FILL: 'MID_FILL',
  LIGHT_FILL: 'LIGHT_FILL',
  LIGHT_EDGE: 'LIGHT_EDGE',
  DARK_FILL: 'DARK_FILL',
  DARK_EDGE: 'DARK_EDGE',
  RAINBOW_FILL: 'RAINBOW_FILL',
  RAINBOW_EDGE: 'RAINBOW_EDGE'
};

export class Logo extends Component {
  render() {
    const renderLogo = () => {
      switch (this.props.style) {
        case PROPS.DARK_FILL:
          return (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="48"
              height="60"
              version="1"
            >
              <g fill="#000" fillRule="evenodd">
                <rect width="13" height="60" rx="6" />
                <rect width="13" height="60" x="35" rx="6" />
              </g>
            </svg>
          );
        case PROPS.RAINBOW_FILL:
          return (
            <svg width="121" height="186" xmlns="http://www.w3.org/2000/svg">
              <defs>
                <linearGradient x1="50%" y1="0%" x2="50%" y2="100%" id="a">
                  <stop stopColor="#9E2D2D" offset="0%" />
                  <stop stopColor="#69B47C" offset="28.474%" />
                  <stop stopColor="#00FFDE" offset="53.703%" />
                  <stop stopColor="#FF00BF" offset="78.753%" />
                  <stop stopColor="#E5FF00" offset="100%" />
                </linearGradient>
              </defs>
              <g fill="url(#a)" fillRule="evenodd">
                <rect width="44" height="186" rx="22" />
                <rect x="77" width="44" height="186" rx="22" />
              </g>
            </svg>
          );
        case PROPS.RAINBOW_EDGE:
          return (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="123"
              height="190"
              version="1"
            >
              <defs>
                <linearGradient id="a" x1="50%" x2="50%" y1="0%" y2="100%">
                  <stop offset="0%" stopColor="#9E2D2D" />
                  <stop offset="28%" stopColor="#69B47C" />
                  <stop offset="54%" stopColor="#00FFDE" />
                  <stop offset="79%" stopColor="#FF00BF" />
                  <stop offset="100%" stopColor="#E5FF00" />
                </linearGradient>
              </defs>
              <g
                fill="none"
                fillRule="evenodd"
                stroke="url(#a)"
                strokeWidth="4"
                transform="translate(2 2)"
              >
                <rect width="44" height="186" rx="22" />
                <rect width="44" height="186" x="75" rx="22" />
              </g>
            </svg>
          );
        default:
          return null;
      }
    };
    return <div className="center-it">{renderLogo()}</div>;
  }
}



