import React, { Component } from 'react';

import { WifiOff } from 'react-feather';

import './Modal.css';
export class ErrorModal extends Component {
  render() {
    return (
      <div className="Modal">
        <div class="textbox">
          <h2>YOU MUST GO OFFLINE TO VIEW THIS PAGE</h2>
          <div
            style={{ display: 'flex', alignItems: 'center', color: '#A1A1A1' }}
          >
            <h4>Disconnect to continue</h4>
            <WifiOff color="#A1A1A1" style={{ marginLeft: '0.4em' }} />
          </div>
        </div>
      </div>
    );
  }
}



