import React, { Component } from 'react';

import { AnimatedEarth } from './AnimatedEarth';
import { ErrorModal } from '../shared/ErrorModal';
import { Online, Offline } from 'react-detect-offline';

export class OfflineWorldScreen extends Component {
  render() {
    let __DEV__ = process.env.NODE_ENV === 'development' ? true : false;
    return (
      <div>
        {__DEV__
          ? <div>
              <Online>
                <AnimatedEarth />
              </Online>
              <Offline>
                <ErrorModal />
              </Offline>
            </div>
          : <div>
              <Online>
                <ErrorModal />
              </Online>
              <Offline>
                <AnimatedEarth />
              </Offline>
            </div>}
      </div>
    );
  }
}



